footer {
  padding: 60px 0 25px;
  background-color: #F6F6F6;
}

.footer-first {
  display: flex;
  justify-content: space-between;
  padding-bottom: 55px;
  margin-bottom: 25px;
  border-bottom: 2px solid #E9E9E9;
}

.footer-first .column {
  width: 24%;
}

.footer-first .column h6 {
  font-size: 15px;
  font-weight: 500;
  color: #1F283C;
  line-height: 20px;
  margin-bottom: 25px;
}

.footer-first .column ul li {
  margin-bottom: 10px;
}

.footer-first .column ul li:last-child {
  margin-bottom: 0;
}

.footer-first .column ul li a {
  font-size: 15px;
  color: #797C84;
  line-height: 20px;
}

.footer-first .column .footer-info_wrap p {
  display: block;
  font-size: 15px;
  line-height: 20px;
  margin-bottom: 10px;
}

.footer-first .column .footer-info_wrap a {
  display: block;
  color: #797C84;
  font-size: 15px;
  line-height: 20px;
  margin-bottom: 10px;
}

.footer-first .column .footer-info_wrap .footer-social {
  margin-top: 15px;
  display: flex;
  margin-bottom: 15px;
}

.footer-first .column .footer-info_wrap .footer-social a {
  display: block;
  width: 40px;
  height: 40px;
  margin-right: 6px;
  margin-bottom: 0;
}

.footer-first .column .footer-info_wrap .footer-social a:last-child {
  margin-right: 0;
}

.footer-first .column .footer-info_wrap .footer-social .facebook {
  background: #E9E9E9 url("../img/Facebook.svg") center no-repeat;
  background-size: 30px;
}

.footer-first .column .footer-info_wrap .footer-social .trip-advisor {
  background: #E9E9E9 url("../img/TripAdvisor.svg") center no-repeat;
  background-size: 30px;
}

.footer-first .column .footer-info_wrap .footer-social .twitter {
  background: #E9E9E9 url("../img/Twitter.svg") center no-repeat;
  background-size: 30px;
}

.footer-first .column .footer-info_wrap .footer-social .instagram {
  background: #E9E9E9 url("../img/Instagram.svg") center no-repeat;
  background-size: 30px;
}

.footer-first .column .footer-info_wrap .footer-social .linkedin {
  background: #E9E9E9 url("../img/LinkedIn.svg") center no-repeat;
  background-size: 30px;
}

.footer-first .column .footer-info_wrap .footer-social .youtube {
  background: #E9E9E9 url("../img/YouTube.svg") center no-repeat;
  background-size: 30px;
}

.footer-first .column .footer-info_wrap .footer-cards {
  display: flex;
}

.footer-first .column .footer-info_wrap .footer-cards img {
  display: block;
}

.footer-first .column .footer-info_wrap .footer-cards img:first-child {
  margin-right: 10px;
}

.footer-second {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.footer-second .footer-second_left {
  display: flex;
  align-items: center;
}

.footer-second .footer-second_left img {
  display: block;
  margin-right: 30px;
}

.footer-second .footer-second_left .copyright {
  font-size: 15px;
  line-height: 20px;
}

.footer-second .developed {
  display: flex;
  left: CENTER;
  font-size: 15px;
  line-height: 20px;
}

.footer-second .developed a {
  display: block;
  margin-left: 5px;
  color: #797C84;
  font-size: 15px;
}

.loading-popup {
  display: none;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 999;
  background: rgba(255, 255, 255, 0.8);
}

.loading-popup .loading-inner {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.cookie-popup {
  display: none;
  position: fixed;
  width: 100%;
  right: 0;
  bottom: 0;
  background-color: #1f283c;
  color: #fff;
  z-index: 999;
}

.cookie-popup .cookie-popup_holder {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 40px 110px;
}

.cookie-popup .left {
  width: 60%;
}

.cookie-popup h4 {
  color: #fff;
  font-size: 24px;
  margin-bottom: 10px;
}

.cookie-popup p {
  line-height: 20px;
}

@media screen and (max-width: 990px) {
  .footer-first {
    flex-wrap: wrap;
  }
  .footer-first .column {
    width: 45%;
    margin-bottom: 40px;
  }
  .footer-first .column:nth-last-child(2), .footer-first .column:last-child {
    margin-bottom: 0;
  }
  .cookie-popup .cookie-popup_holder {
    padding: 40px 30px;
  }
}

@media screen and (max-width: 500px) {
  footer {
    padding: 30px 0;
  }
  .footer-first {
    border-bottom: none;
    margin-bottom: 0;
    padding-bottom: 0;
  }
  .footer-first .column {
    width: 100%;
    padding-bottom: 25px;
    margin-bottom: 25px;
    border-bottom: 2px solid #E9E9E9;
  }
  .footer-first .column:last-child, .footer-first .column:nth-last-child(2) {
    margin-bottom: 25px;
    padding-bottom: 25px;
  }
  .footer-first .column h6 {
    font-size: 14px;
    margin-bottom: 15px;
    line-height: 20px;
  }
  .footer-first .column ul li {
    margin-bottom: 8px;
  }
  .footer-first .column ul li a {
    font-size: 14px;
    line-height: 20px;
  }
  .footer-first .column .footer-info_wrap a {
    color: #23A8CD;
  }
  .footer-first .column .footer-info_wrap .footer-social {
    padding-top: 25px;
    margin-top: 25px;
    border-top: 2px solid #E9E9E9;
  }
  .footer-second {
    flex-direction: column;
    align-items: flex-start;
  }
  .footer-second .footer-second_left img {
    display: none;
  }
  .footer-second .footer-second_left .copyright {
    font-size: 14px;
    margin-bottom: 8px;
  }
  .footer-second .developed {
    font-size: 14px;
  }
  .footer-second .developed a {
    color: #333;
    font-weight: 500;
  }
  .cookie-popup .cookie-popup_holder {
    padding: 30px 15px;
    flex-direction: column;
    text-align: center;
  }
  .cookie-popup .left {
    width: 100%;
    margin-bottom: 20px;
  }
}

